import React, { useContext, Fragment } from 'react'
import { ThemeContext, ResponsiveContext } from 'grommet'
import { MergePropsIntoChildren } from '../'
//import { FirebaseContext } from 'components/utils/Firebase/index.tsx'

import { clone } from 'ramda'

const WithTheme = ({...props}) => {
  const { children } = props
  const addedProps = {
    theme: useContext(ThemeContext),
    responsive: useContext(ResponsiveContext),
    firebase: useContext(FirebaseContext),
  }
  return (
    <MergePropsIntoChildren addedProps={addedProps} {...props}>
      {children}
    </MergePropsIntoChildren>
  )
}


export default WithTheme
