import React from 'react'
import { useScroll } from 'react-viewport-utils'
import { MergePropsIntoChildren } from '../'

export default (props) => {
  const { children } = props
  const addedProps = {
    viewport: useScroll({ priority: 'low' }),
  }
  return (
    <MergePropsIntoChildren addedProps={addedProps} {...props}>
      {children}
    </MergePropsIntoChildren>
  )
}
